/* eslint-disable array-callback-return */
/* eslint-disable eqeqeq */
import React, { useState, useEffect } from "react";
import fetch from "node-fetch";
import "./App.css";

export default function App(){

  const [data, setData] = useState([]);
  const [search, setSearch] = useState("");
  const [length, setLength] = useState(0);
  const [load, setLoad] = useState(10);

  useEffect(() => {
    fetch(`https://raw.githubusercontent.com/akikungz/raw-file-host/main/data.json`).then(res => res.json()).then(res => {
    // fetch(`http://192.168.1.7:8080`).then(res => res.json()).then(res => {
      console.log(res);
      setData(res.data);
      setLength(res.data.length);
    }).catch(console.error);
  }, []);

  function loadBtn(){
    if(length > 0){
      if(length > load){
        return <button 
          className="btn btn-primary shadow-sm search-sticky" 
          style={{width: "100%"}}
          onClick={() => setLoad(load + 10)}
        >Load more...</button>
      }else {
        return undefined;
      }
    }
    return undefined;
  }

  function filterData(){
    const filter = data.filter(value => {
      if(search == "") return value;
      if(value.key.toLowerCase().includes(search.toLowerCase())){
        return value;
      }
    });

    if(length != filter.length) setLength(filter.length);
    return filter;
  }

  return (
    <div className="container">
      <h4>เฉลย Virtual School ม.6</h4>
      <div className="group">
        <input type="text" className="form-control" onChange={({target}) => setSearch(target.value)} placeholder="Key..." />
        <span className="position-absolute length-badge badge rounded-pill bg-success">{ length }</span>
      </div>
      {/* <span>All result {length} data</span> */}
      <table className="table table-hover table-dark">
        <thead className="table-secondary sticky-thead">
          <tr>
            <th scope="col">Key</th>
            <th scope="col">Value</th>
          </tr>
        </thead>
        <tbody>
          {
            filterData().map((res, i) => {
              if(i < load) return <tr><td>{res.key}</td><td>{res.value}</td></tr>
            })
          }
          { !length ? <tr><td colSpan={2}>Undefined</td></tr> : undefined }
        </tbody>
      </table>
      { loadBtn() }
    </div>
  )
}